.outdoorCard{
    background-color: red;
    width: 771px;
    height: 471px;
    border-radius: 2%;
    display: grid;
    padding-left: 55px;
    margin: 0;
    align-items: center;
    grid-template-columns: repeat(6), 1fr;
    grid-template-rows: 2fr 1fr;
}

.bigImage{
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 3;
}

.outdoorContainer{
    grid-column-start: 4;
    grid-column-end: 7;
    grid-row-start: 1;
    grid-row-end: 2;
}

.category{
    background-color: #005AEE;
    padding: 12px;
    color: white;
    font-weight: bold;
    text-transform: uppercase;
    border: none;
    letter-spacing: 3px;
    border-radius: 50px;
    text-align: center;
    width: 120px;
}

.title{
    font-size: 45px;
    margin-right: 200px;
}

.image1{
    grid-column-start: 4;
    grid-column-end: 5;
    grid-row-start: 2;
    grid-row-end: 3;
}

.image2{
    grid-column-start: 5;
    grid-column-end: 6;
    grid-row-start: 2;
    grid-row-end: 3;
}

.image3{
    grid-column-start: 6;
    grid-column-end: 7;
    grid-row-start: 2;
    grid-row-end: 3;
}
